import './styles.css'



window.onload = function () {
	let menuIcon = document.querySelector('.menu__icon');
	let menuBody = document.querySelector('.menu__body');

	if (menuIcon) {
		menuIcon.addEventListener('click', function () {
			menuIcon.classList.toggle('_active');
			menuBody.classList.toggle('_active');
			document.body.classList.toggle('_lock');
		})
	}

	// Form 

	const form = document.querySelector('.form');
	const submitButton = form.querySelector('.submit-button')

	form.addEventListener('submit', sendForm)

	// Send Form

	async function sendForm(e) {
		e.preventDefault();

		let isFormValid = validateForm(form);
		let formData = new FormData(form);

		if (isFormValid === true) {
			let url = 'http://localhost:8081/index.php';
			let response = await fetch(url, {
				method: "POST",
				headers: {
					'Content-Type': 'application/json;charset=utf-8'
				},
				body: formData,
			});
			if (response.ok) {
				form.reset();
				document.querySelector('.sending-error').innerHTML += ' <span class="validation-error">Форма отправлена</span>';
			} else {
				removeErrors('validation-error');
				document.querySelector('.sending-error').innerHTML += ' <span class="validation-error">Произошла ошибка при отправке формы</span>';
			}
		}

	}

	// Form Validation

	function validateForm(form) {

		let isFormValid = true;

		removeErrors('validation-error__email');

		const emailField = form.querySelector('#email');
		if (!emailField.value) {
			form.querySelector('.validation-error').innerHTML = '<span class="validation-error__email">Enter your email</span>';
			isFormValid = false;
		} else {
			const re = /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
			if (!re.test(String(emailField.value).toLowerCase())) {
				form.querySelector('.validation-error').innerHTML = '<span class="validation-error__email">Incorrect email</span>'
				isFormValid = false;
			}
		}
		return isFormValid;
	}

	function removeErrors(className) {
		const errors = document.getElementsByClassName(className);
		while (errors.length > 0) {
			errors[0].parentNode.removeChild(errors[0]);
		}
	}

}



